.draggable {
	@extend .card;
	background-color: transparent;
	* {
		@include trans();
	}
	&-container {
		border: 2px dashed $gray-light;
		border-radius: 3px;
		min-height: 300px;
	}
	&-block {
		// @extend .card-block;
		margin: $padding-sm;
	}
	&-title {
		position: absolute;
		@include center();
	}
	&-subtitle {
		@extend .text-muted;
	}
	&-item {
		@extend .list-group-item;
		cursor: move;
		&, &:last-child {
			margin-bottom: $margin;
		}
		.draggable-subtitle {
			padding-left: $padding-lg;
			margin-bottom: 0;
		}
	}
	.separator {
		margin-top: $margin-sm;
	}
}

.toolbox .draggable-block .draggable-item {
	margin-bottom: -1px;
}
