body.dark-theme {

	@include colors($white-color, $dark-color, false);

}

.sidebar.sidebar-dark {
	background: $dark-color;
	.sidebar-link,
	.dropdown-menu .dropdown-item,
	.dropdown-menu .dropdown-item .dropdown-link {
		color: $dark-color-color;

		@include haf($dark-color-color, darken($dark-color, 0.9));
	}

	.dropdown-menu {
		background-color: darken($dark-color, 0.9);
	}

	.dropdown-menu .dropdown-item,
	.dropdown-menu .dropdown-item .dropdown-link {
		@include haf($dark-color-color, transparentize(black, 0.9));
	}

	.dropdown-divider {
		background-color: transparentize(black, 0.9);
	}

	.active {
		@include colors($dark-color-color, transparentize(black, 0.9));
	}
}